class Organization {
    id: number | undefined | null;
    name: any;
    description: any;

    constructor() {
        this.id = undefined;
        this.name = "";
        this.description = "";
    }
}

class Center {
    id: number | undefined | null;
    name: any;
    description: any;

    constructor() {
        this.id = undefined;
        this.name = "";
        this.description = "";
    }
}

class ParticipantUnit {
    organization: Organization;
    center: Center;

    constructor() {
        this.organization = new Organization();
        this.center = new Center();
    }
}

class Participant extends ParticipantUnit {

    family_name: string;
    first_name: string;
    height: string;
    weight: string;
    gender: string;
    google_account: string;
    description: string; 

    constructor() {
        super();

        this.family_name = '';
        this.first_name = '';
        this.height = '';
        this.weight = '';
        this.gender = '';
        this.google_account = '';
        this.description = '';
    }
}

class Experiment {
    name: any;
    description: string;
    batches: Batch[];

    constructor(params?: Experiment) {
        this.name = params? params.name : '';
        this.description = params? params.description : '';
        this.batches = params? params.batches : [new Batch()];
    }
}

class Batch extends ParticipantUnit {

    participants: Participant[] | undefined;
    participants_list?: any;

    constructor() {
        super();

        this.participants = undefined;
        // this.participants_list = undefined;
    }
}


const guest_list = ref<any[]>([]);

const experimentFormVisible = ref(false);
const experimentDataFetched = ref<boolean>(true);

const experimentsList = ref<any[]>([]);
const experimentsListFetched = ref<boolean>(false);
const organizationsList = ref<any[]>([]);
const centersList = ref<any[]>([]);

const experimentFromValues = ref<Experiment | any>();
const participantFormValues = ref<Participant | any>();


const handleAddBatch = () => {
    experimentFromValues.value?.batches.push(new Batch());
};

const handleRemoveBatch = (index: number) => {
    experimentFromValues.value?.batches.splice(index, 1);
};

const handleFormOpen = (form: 'experiment' | 'participant', id?: number) => {
    if (form === 'experiment') {
        experimentFromValues.value = new Experiment();
        experimentFormVisible.value = true;
    } else if (form === 'participant') {
        guestFetched.value = false;
        
        if (id) {
            formMode.value = 'edit';
            getParticipantById(id);
        } else {
            participantFormValues.value = new Participant();
            guestFetched.value = true;
        }
        modalVisible.value = true;
    }
};

const handleFormClose = (form: 'experiment' | 'participant') => {
    if (form === 'experiment') {
        experimentFormVisible.value = false;
    } else if (form === 'participant') {
        modalVisible.value = false;
    }
};

const handleExperimentFormSubmit = async(formdata: Experiment) => {

    const newVariable: Experiment = {
        name: formdata.name,
        description: formdata.description,
        batches: [],
    };

    await Promise.all(
        formdata.batches.map(async (batch, index) => {
            await onParseParticipantList(batch.participants_list[0].file).then((response) => {
                newVariable.batches.push({
                    organization: {
                        id: batch.organization.id,
                        name: batch.organization.name,
                        description: batch.organization.description,
                    },
                    center: {
                        id: batch.center.id,
                        name: batch.center.name,
                        description: batch.center.description,
                    },
                    participants: response,
                })
            })
        })
    )

    // console.log('out', newVariable)
    addExperiment(newVariable);

    // need to solve:
    // header is \r\n, but content is \n
    // so dont use header: true, just parse by a new fucniton

    // setTimeout(() => {
    // console.log('new', newVariable)
    // }, 500);
};

const onParseParticipantList = async (file: any): Promise<Participant[]> => {

    return new Promise ((resolve, rejects) => {
        Papa.parse(
            file,
            {
                // header: true,
                delimiter: ',',
                // newline: '\n',
                skipEmptyLines: true,
                complete: (results, file) => {
                    const header = results.data[0];
                    const infos = results.data.slice(1, results.data.length);

                    const parsed_list: any[] = [];

                    infos.map((info: any) => {
                        const new_participant = {
                            family_name: info[0],
                            first_name: info[1],
                            height: info[2],
                            weight: info[3],
                            gender: info[4],
                            description: info[5],
                            google_account: info[6],
                        };
                        parsed_list.push(new_participant);
                    })

                    resolve(parsed_list);
                },
                error: (err, file) => {
                    console.error('CSV Parsing error', err);
                    rejects(err);
                }
            }
        );
    })
};



const handleParticipantFormSubmit = (formdata: any) => {
    console.log("submit", formdata);
    if (formMode.value === 'add') {
        addGuest(formdata);
    } else if (formMode.value === 'edit') {
        updateParticipantById(formdata);
    }
    handleFormClose('participant');
};


