/* APIs */
const getAllExperiments = () => {

    const params = {

    };

    API.balnet.experiments.miband.getAllExperiments(params).then((response) => {

        console.log('getAllExperiments() response', response);
        if (response.meta.code === 200) {
            experimentsList.value = response.data.experiments_list;
            experimentsListFetched.value = true;
        } 
    }).catch((error) => {
        console.error('getAllExperiments() error', error);
    });
};

const getAllOrganizations = () => {

    const params = {

    };

    API.balnet.experiments.miband.getAllOrganizations(params).then((response) => {

        // console.log('getAllOrganizations() response', response);
        if (response.meta.code === 200) {
            organizationsList.value = response.data.organizations_list;
        } 
    }).catch((error) => {
        console.error('getAllOrganizations() error', error);
    });
};

const getAllCenters = () => {

    const params = {

    };

    API.balnet.experiments.miband.getAllCenters(params).then((response) => {

        // console.log('getAllCenters() response', response);
        if (response.meta.code === 200) {
            centersList.value = response.data.centers_list;
        }
    }).catch((error) => {
        // console.error('getAllCenters() error', error);
    });
};

const addExperiment = (formdata: Experiment) => {

    const params = {
        experiment: {
            ...formdata
        },
    };

    console.log('addExperiment() params', params);
    API.balnet.experiments.miband.addExperiment(params).then((response) => {

        console.log('addExperiment() response', response);
        if (response.meta.code === 200) {
            alert(response.meta.message);
            handleFormClose('experiment');
        } else {
            alert(response.meta.message);
        }

    }).catch((error) => {
        console.error('addExperiment() error', error);
    });
};



const getAllGuests = () => {

    const params = {

    };

    API.balnet.experiments.miband.getAllGuests(params).then((response) => {

        console.log('getAllGuests() response', response);
        if (response.meta.code === 200) {
            guest_list.value = response.data.guest_list;
        }
    }).catch((error) => {
        console.error('getAllGuests() error', error);
    });
};

const getParticipantById = (id: number) => {

    const params = {
        id: id,
    };

    // console.log('getParticipantById() params', params);
    API.balnet.experiments.miband.getParticipantById(params).then((response) => {

        console.log('getParticipantById() response', response);
        guest.value = response.data.participant;
        guestFetched.value = true;

    }).catch((error) => {
        console.error('getParticipantById() error', error);
    });
};

const addGuest = (formdata: any) => {

    const params = {
        ...formdata,
    };

    // console.log('addGuest() params', params);
    API.balnet.experiments.miband.addGuest(params).then((response) => {

        console.log('addGuest() response', response);
        getAllGuests();
    }).catch((error) => {
        console.error('addGuest() error', error);
    });
};

const updateParticipantById = (formdata: any) => {

    const params = {
        id: guest.value.id,
        ...formdata,
    };

    console.log('updateParticipantById() params', params);
    API.balnet.experiments.miband.updateParticipantById(params).then((response) => {

        console.log('updateParticipantById() response', response);
        getAllGuests();
    }).catch((error) => {
        console.log('updateParticipantById() error', error);
    });
};


